  /*  */
  input[type="checkbox"] {
    -webkit-appearance: none;  /* Webkit */
    -moz-appearance: none;     /* Firefox */
    appearance: none;          /*  */
    width: rem(20);               /*  */
    height: rem(20);              /*  */
    border: rem(1) solid $green;    /*  */
    border-radius: 0;        /*  */
    position: relative;        /*  */
    cursor: pointer;          /*  */
    background-color: $white;   /*  */
    top: rem(5);
  }
  
  /*  */
  input[type="checkbox"]:checked {
    background-color:$white;  /*  */
    border-color: $green;      /*  */
  }
  
  /*  */
  input[type="checkbox"]:checked::after {
    background-color: transparent;
  border-bottom: 0.125rem solid #C94800; /*  */
  border-right: 0.125rem solid #C94800; /*  */
  content: "";
  height: 1rem;
  left: 0.4375rem;
  position: absolute;
  top: 0;
  transform: rotate(55deg);
  width: 0.3125rem;
  }
  
  


  
